<template>
    <div class="product-detail">
      <div class="product-images">
        <img :src="product.image" :alt="product.name">
      </div>
      <div class="product-info">
        <h1>{{ product.name }}</h1>
        <div class="product-meta">
          <div class="rating">
            <span>Rating: {{ product.rating }}/5</span>
            <span>({{ product.reviews.length }} reviews)</span>
          </div>
          <span class="category">Category: {{ product.category }}</span>
        </div>
  
        <div class="product-price">
          <h2>{{ formatCurrency(product.price) }}</h2>
          <span class="stock" :class="{ 'low-stock': product.stock < 10 }">
            {{ product.stock }} items in stock
          </span>
        </div>
  
        <div class="product-description">
          <h3>Description</h3>
          <p>{{ product.description }}</p>
        </div>
  
        <div class="purchase-actions">
          <div class="quantity-selector">
            <button @click="decreaseQuantity">-</button>
            <input type="number" v-model="quantity" min="1" :max="product.stock">
            <button @click="increaseQuantity">+</button>
          </div>
  
          <button 
            class="add-to-cart"
            @click="addToCart"
            :disabled="!product.stock"
          >
            Add to Cart
          </button>
        </div>
      </div>
  
      <div class="product-reviews">
        <h3>Customer Reviews</h3>
        
        <div v-if="isLoggedIn" class="review-form">
          <h4>Write a Review</h4>
          <div class="rating-input">
            <span>Your Rating:</span>
            <select v-model="newReview.rating">
              <option v-for="n in 5" :key="n" :value="n">{{ n }} stars</option>
            </select>
          </div>
          <textarea 
            v-model="newReview.comment"
            placeholder="Write your review here..."
          ></textarea>
          <button @click="submitReview">Submit Review</button>
        </div>
  
        <div class="reviews-list">
          <div v-for="review in product.reviews" :key="review.userId" class="review">
            <div class="review-header">
              <span class="rating">{{ review.rating }} stars</span>
              <span class="date">{{ formatDate(review.date) }}</span>
            </div>
            <p class="comment">{{ review.comment }}</p>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script setup>
  import { ref, computed, defineProps } from 'vue'
  import { useStore } from 'vuex'
  
  const props = defineProps({
    product: {
      type: Object,
      required: true
    }
  })
  const store = useStore()
  const quantity = ref(1)
  const newReview = ref({
    rating: 5,
    comment: ''
  })
  
  const isLoggedIn = computed(() => store.getters['auth/isLoggedIn'])
  
  const increaseQuantity = () => {
    if (quantity.value < props.product.stock) {
      quantity.value++
    }
  }
  
  const decreaseQuantity = () => {
    if (quantity.value > 1) {
      quantity.value--
    }
  }
  
  const addToCart = () => {
    store.dispatch('cart/addItem', {
      id: props.product.id,
      name: props.product.name,
      price: props.product.price,
      image: props.product.image,
      quantity: quantity.value
    })
  }
  
  const submitReview = async () => {
    if (!newReview.value.comment) return
  
    await store.dispatch('product/addReview', {
      productId: props.product.id,
      review: {
        ...newReview.value,
        date: new Date().toISOString()
      }
    })
  
    newReview.value = {
      rating: 5,
      comment: ''
    }
  }
  </script>
  <style scoped>
  .product-detail {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: var(--spacing-xl);
    padding: var(--spacing-xl);
  }
  
  .product-images img {
    width: 100%;
    border-radius: var(--radius-md);
    box-shadow: var(--shadow-md);
  }
  
  .product-info {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-md);
  }
  
  .product-meta {
    display: flex;
    justify-content: space-between;
    color: var(--text-color);
    font-size: var(--text-sm);
  }
  
  .product-price {
    margin: var(--spacing-md) 0;
  }
  
  .product-price h2 {
    color: var(--primary-color);
    font-size: var(--title);
  }
  
  .stock {
    font-size: var(--text-sm);
    color: var(--secondary-color);
  }
  
  .low-stock {
    color: var(--accent-color);
  }
  
  .purchase-actions {
    display: flex;
    gap: var(--spacing-md);
    margin: var(--spacing-lg) 0;
  }
  
  .quantity-selector {
    display: flex;
    align-items: center;
    gap: var(--spacing-sm);
  }
  
  .quantity-selector button {
    padding: var(--spacing-sm) var(--spacing-md);
    border: 1px solid var(--primary-color);
    background: white;
    border-radius: var(--radius-sm);
    cursor: pointer;
  }
  
  .quantity-selector input {
    width: 60px;
    text-align: center;
  }
  
  .add-to-cart {
    padding: var(--spacing-sm) var(--spacing-xl);
    background: var(--primary-color);
    color: white;
    border: none;
    border-radius: var(--radius-sm);
    cursor: pointer;
  }
  
  .add-to-cart:disabled {
    background: #ccc;
    cursor: not-allowed;
  }
  
  .product-reviews {
    grid-column: 1 / -1;
    margin-top: var(--spacing-xl);
  }
  
  .review-form {
    background: var(--gray-bg);
    padding: var(--spacing-lg);
    border-radius: var(--radius-md);
    margin: var(--spacing-lg) 0;
  }
  
  .rating-input {
    margin: var(--spacing-md) 0;
  }
  
  .review-form textarea {
    width: 100%;
    min-height: 100px;
    margin: var(--spacing-sm) 0;
  }
  
  .reviews-list {
    display: grid;
    gap: var(--spacing-md);
  }
  
  .review {
    padding: var(--spacing-md);
    border: 1px solid #eee;
    border-radius: var(--radius-sm);
  }
  
  .review-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: var(--spacing-sm);
    font-size: var(--text-sm);
  }
  </style>
  